$primary: #10b981;
$primary-bg: #f5f5f5;
$textprimarycolor: #2b2626;
$primary-products: #845ec2;


.card {
  width: 361px;
  display: flex;
  flex-direction: column;
  gap: 29px;
  position: relative; }

.card__product {
  width: 100%;
  height: 438px;
  padding: 30px 21px 18px 21px;
  background-color: #ecfef4;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; }

.card__product_blue {
  background-color: #c9f2ff; }

.card__product_purp {
  background-color: #e5d5ff; }

.card__img {
  width: 196px;
  height: 196px; }

.card__descr {
  width: 100%;
  height: 164px;
  background-color: #fff;
  border-radius: 15px;
  padding: 30px 51px;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }

.card__text {
  color: $textprimarycolor;
  font-weight: 500;
  font-size: 21px;
  line-height: 35px; }

.card__rating {
  display: inline-flex;
  justify-content: baseline; }

.card__rating-count {
  display: inline-flex;
  color: #888888;
  font-weight: 500;
  font-size: 11px;
  line-height: 35px;
  margin-left: 13px; }

.card__btn {
  width: 100%; }

.card__discount {
  position: absolute;
  right: 19px;
  top: 13px;
  background-color: $primary;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px; }

.card__discount > p {
  width: 60%;
  text-align: center; }

.card__discount_purp {
  background-color: #845ec2; }

